<template>
  <!-- 页面可选组件列表 -->
  <div class="component-item">
    <div v-for="(item,index) in componentlist" :key="index">
      <h2 class="component-title">{{item.title}}</h2>
      <ul class="component-list">
        <li v-for="(component,size) in item.components" :key="size" @click="addComponent(component)">
          {{component.name}}
        </li>
      </ul>
    </div>
  </div><!-- 页面可选组件列表 end -->
</template>

<script>
  export default {
    name: 'choosecomponents',
    data() {
      return {
        // 页面所有可选组件列表
        componentlist: [
          {
            title: '基础组件',
            components: [
              {
                type: 'swiper',
                name: '切换广告',
                data: {
                  piclist: [
                    {
                      src: '',
                      title: '',
                      linkurl: ''
                    }
                  ],
                  layout: 'swiper'
                }
              },
              {
                type: 'navigation',
                name: '图文导航',
                data: {
                  layout: 'pic',
                  rows: '1',
                  columns: '4',
                  backgroundColor: '#fff',
                  textColor: '#222',
                  piclist: [
                    {
                      src: '',
                      title: '',
                      linkurl: ''
                    }
                  ]
                }
              },
              {
                type: 'vtitle',
                name: '标题',
                data: {
                  title: '标题',
                  position: 'center',
                  backgroundColor: '#fff'
                }
              },
              {
                type: 'productlist',
                name: '商品',
                data: {
                  source: 'product',
                  style: '1'
                }
              },
              {
                type: 'notice',
                name: '公告',
                data: {
                  backgroundColor: '#fff7cc',
                  textColor: '#666'
                }
              },
              {
                type: 'magiclayout',
                name: '魔方',
                data: {
                  layout: 'column-2'
                }
              },
              {
                type: 'timelimitsale',
                name: '限时折扣'
              },
              {
                type: 'assistblank',
                name: '辅助空白',
                data: {
                  height: 10
                }
              }
            ]
          }
        ]
      }
    },
    methods: {
      // 点击某个组件进行添加
      addComponent(data) {
        this.$emit('addComponent', data)
      }
    }
  }
</script>

<style scoped>
  .component-item {
    padding: 0 10px 20px;
    overflow: hidden;
  }

  .component-title {
    font-size: 12px;
    margin: 13px 0 8px;
  }

  .component-list {
    overflow: hidden;
  }

  .component-list li {
    float: left;
    margin: 0 0 7px 2%;
    width: 32%;
    border: 1px dashed #e5e5e5;
    color: #666;
    background: #f8f8f8;
    text-align: center;
    line-height: 30px;
    box-sizing: border-box;
    cursor: pointer;
  }

  .component-list li:hover {
    background: #e8f7fd;
    border-color: #bdf;
    color: #38f
  }

  .component-list li:nth-child(3n+1) {
    margin-left: 0
  }
</style>



