<template>
  <el-form :model="parmes" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="空白高度:">
      <el-slider v-model="parmes.height"></el-slider>
      <span class="ml_10">{{parmes.height}} 像素</span>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'assistblank',
    props: ['parmes']
  }
</script>

<style>
  .el-slider {
    display: inline-block;
    width: 78%;
    vertical-align: middle;
  }
  .el-slider__runway {
    margin: 12px 0;
  }
</style>



