<template>
  <!-- 店铺导航 -->
  <nav class="store-nav" :style="{backgroundColor: parmes.backgroundColor}" :class="styleClass">
    <!--<a :href="item.linkurl" :style="{color: parmes.textColor,width: itemWidth + '%'}" v-for="(item,index) in parmes.piclist" :key="index">-->
      <!--<s><img :src="item.src?item.src:'./nav_1.png'"></s>-->
      <!--{{item.title?item.title:'导航名称'}}-->
    <!--</a>-->
    <template v-if="parmes.layout === 'pic'">
      <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
        <s><img src="./nav_1.png"></s>
        导航名称
      </a>
      <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
        <s><img src="./nav_2.png"></s>
        导航名称
      </a>
      <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
        <s><img src="./nav_3.png"></s>
        导航名称
      </a>
      <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
        <s><img src="./nav_4.png"></s>
        导航名称
      </a>
      <template v-if="parmes.rows === '2'">
        <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
          <s><img src="./nav_5.png"></s>
          导航名称
        </a>
        <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
          <s><img src="./nav_1.png"></s>
          导航名称
        </a>
        <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
          <s><img src="./nav_2.png"></s>
          导航名称
        </a>
        <a href="##" :style="{color: parmes.textColor,width: itemWidth + '%'}">
          <s><img src="./nav_3.png"></s>
          导航名称
        </a>
      </template>
    </template>
    <template v-else>
      <a href="##" :style="{color: parmes.textColor}">
        导航名称
      </a>
      <a href="##" :style="{color: parmes.textColor}">
        导航名称
      </a>
      <a href="##" :style="{color: parmes.textColor}">
        导航名称
      </a>
      <a href="##" :style="{color: parmes.textColor}">
        导航名称
      </a>
    </template>
  </nav><!-- 店铺导航 end -->
</template>

<script>
  export default {
    name: 'navigation',
    props: ['parmes'],
    computed: {
      styleClass() {
        if (this.parmes.layout === 'txt') {
          return 'singletxt'
        }
        if (this.parmes.rows === '1') {
          return 'singlerow'
        }
        return 'mulitrow'
      },
      itemWidth() {
        return 100 / parseInt(this.parmes.columns)
      }
    }
  }
</script>

<style scoped>
  .store-nav {
    display: flex;
    justify-content: center;
    background: #fff;
    padding: 0 .5rem;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: .5rem;
  }

  .store-nav a {
    display: block;
    padding: .4rem 0;
    text-align: center;
  }

  .store-nav s {
    display: block;
    width: 48px;
    height: 48px;
    margin: 0 auto .1rem;
  }

  .store-nav s img {
    width: 100%;
    height: 100%;
  }

  /*1行显示*/
  .singlerow a {
    flex: 1;
  }

  /*2行显示*/
  .mulitrow {
    flex-wrap: wrap;
  }

  /*文本导航*/
  .singletxt a {
    position: relative;
    flex: 1;
  }
  .singletxt a:before {
    content: '';
    position: absolute;
    left: 0;
    top: 20%;
    bottom: 20%;
    width: 1px;
    background: #e5e5e5;
  }
  .singletxt a:first-child:before {
    display: none;
  }
</style>



