<template>
  <div>
    <el-form label-width="70px" class="demo-ruleForm">
      <el-form-item label="选择模板:">
        <ul class="layout-list">
          <li v-for="item in layoutList" :key="item.id" :class="{'active':parmes.layout == item.id}"
              @click="parmes.layout = item.id">
            <div class="pic"><img :src="item.icon"></div>
            <p class="name">{{item.name}}</p>
          </li>
        </ul>
      </el-form-item>
      <el-form-item label="布局:">
        <div class="mg-box mg-flex">
          <div class="mg-item"></div>
          <div class="mg-item"></div>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import ico1 from 'componentsconfig/magiclayout/lauout-mg-1.png'
  import ico2 from 'componentsconfig/magiclayout/lauout-mg-2.png'
  import ico3 from 'componentsconfig/magiclayout/lauout-mg-3.png'
  import ico4 from 'componentsconfig/magiclayout/lauout-mg-4.png'
  import ico5 from 'componentsconfig/magiclayout/lauout-mg-5.png'
  import ico6 from 'componentsconfig/magiclayout/lauout-mg-6.png'
  import ico7 from 'componentsconfig/magiclayout/lauout-mg-7.png'
  import ico8 from 'componentsconfig/magiclayout/lauout-mg-8.png'

  export default {
    name: 'magiclayout',
    props: ['parmes'],
    data() {
      return {
        // 支持的模板类型
        layoutList: [
          {
            name: '1行2个',
            id: 'column-2',
            icon: ico1
          },
          {
            name: '1行3个',
            id: 'column-3',
            icon: ico2
          },
          {
            name: '1行4个',
            id: 'column-4',
            icon: ico3
          },
          {
            name: '2左2右',
            id: 'lr-2-2',
            icon: ico4
          },
          {
            name: '1左2右',
            id: 'lr-1-2',
            icon: ico5
          },
          {
            name: '1上2下',
            id: 'tb-1-2',
            icon: ico6
          },
          {
            name: '1左3右',
            id: 'lr-1-3',
            icon: ico7
          },
          {
            name: '自定义',
            id: 'mg-custom',
            icon: ico8
          }
        ]
      }
    }
  }
</script>

<style scoped>
  .mg-box {

  }
  .mg-item {

  }
  .mg-flex {
    display: flex;
  }
</style>



