<template>
  <div>
    <el-form :model="parmes" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="公告内容" required>
        <el-button size="mini" icon="el-icon-plus" @click="dialogFormVisible = true">添加公告内容</el-button>
      </el-form-item>
      <el-form-item label="背景颜色">
        <el-color-picker v-model="parmes.backgroundColor"></el-color-picker>
      </el-form-item>
      <el-form-item label="文字颜色">
        <el-color-picker v-model="parmes.textColor"></el-color-picker>
      </el-form-item>
    </el-form>

    <!-- 选择公告内容模态框 -->
    <el-dialog title="选择公告内容" :visible.sync="dialogFormVisible" class="table-dialog" width="900px">
      <div slot="footer" class="dialog-footer">
        <el-button type="primary">确 定</el-button>
        <el-button @click="dialogFormVisible = false">取 消</el-button>
      </div>
    </el-dialog>
    <!-- 选择公告内容模态框 end -->
  </div>
</template>

<script>
  export default {
    name: 'notice',
    props: ['parmes'],
    data() {
      return {
        dialogFormVisible: false, // 选择公告模态框是否显示
        noticeSearchTxt: '' // 公告名称筛选内容
      }
    }
  }
</script>

<style scoped>

</style>



