<template>
  <!-- 滚动广告  -->
  <section class="store-swiper">
    <div class="single-list" v-if="parmes.layout == 'single'">
      <div class="single-item" v-for="(item,index) in parmes.piclist" :key="index">
        <a :href="item.linkurl?item.linkurl:'##'">
          <img :src="item.src">
          <p v-if="item.title" class="swiper-title">{{item.title}}</p>
        </a>
      </div>
    </div>
    <swiper v-if="parmes.layout == 'swiper'" :options="swiperOption">
      <swiper-slide v-for="(item,index) in parmes.piclist" :key="index">
        <a :href="item.linkurl?item.linkurl:'##'">
          <img :src="item.src">
          <p v-if="item.title" class="swiper-title">{{item.title}}</p>
        </a>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </section>
  <!-- 滚动广告 end  -->
</template>

<script type="text/ecmascript-6">
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'

  export default {
    name: 'vswiper',
    props: ['parmes'],
    data() {
      return {
        swiperOption: {
          notNextTick: true,
          autoplay: true,
          pagination: {
            el: '.swiper-pagination'
          }
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    }
  }
</script>

<style scoped>
  .store-swiper {
    width: 100%;
    height: 7.7rem;
    text-align: center
  }

  .store-swiper img {
    width: 100%;
    height: 7.7rem;
  }

  .swiper-pagination-bullet-active {
    background: #fe5478;
  }

  .swiper-title {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1.2rem;
    line-height: 1.2rem;
    padding: 0 .5rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    background: rgba(0,0,0,.5);
    color: #ffffff;
    box-sizing: border-box;
  }
  .swiper-container .swiper-title {
    text-align: left;
  }
</style>



