<template>
  <div>
    <el-form :model="parmes" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="选择模板">
        <ul class="layout-list">
          <li v-for="item in layoutList" :key="item.id" :class="{'active':parmes.layout == item.id}"
              @click="parmes.layout = item.id">
            <div class="pic"><img :src="item.icon"></div>
            <p class="name">{{item.name}}</p>
          </li>
        </ul>
      </el-form-item>
      <template v-if="parmes.layout === 'pic'">
        <el-form-item label="选择行数">
          <el-radio-group v-model="parmes.rows">
            <el-radio label="1">1行显示</el-radio>
            <el-radio label="2">2行显示</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="每行显示" v-if="parmes.rows === '2'">
          <el-select v-model="parmes.columns">
            <el-option
              v-for="item in columnList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </template>
      <el-form-item label="背景颜色">
        <el-color-picker v-model="parmes.backgroundColor"></el-color-picker>
      </el-form-item>
      <el-form-item label="文字颜色">
        <el-color-picker v-model="parmes.textColor"></el-color-picker>
      </el-form-item>
    </el-form>
    <ul class="up-pic-list">
      <li v-for="(item,index) in parmes.piclist" :key="index">
        <div class="pic">
          <img v-if="item.src" :src="item.src">
          <div v-else class="add-item-pic">
            <i class="el-icon-plus"></i>
            添加图片
          </div>
        </div>
        <div class="info">
          <el-form :model="item" label-width="50px" class="demo-ruleForm">
            <el-form-item label="标题">
              <el-input v-model="item.title"></el-input>
            </el-form-item>
            <el-form-item label="链接">
              <el-input v-model="item.link"></el-input>
            </el-form-item>
          </el-form>
        </div>
      </li>
    </ul>
    <div class="add-pic">
      <p><i class="el-icon-plus"></i> 添加一个广告图</p>
      建议尺寸 750x350 像素
    </div>
    <p class="add-info">最多添加10个广告，拖动图片广告可对其排序</p>
  </div>
</template>

<script>
  import ico1 from 'componentsconfig/navigation/lauout-nav-pic.png'
  import ico2 from 'componentsconfig/navigation/lauout-nav-txt.png'

  export default {
    name: 'navigation',
    props: ['parmes'],
    data() {
      return {
        // 支持的模板类型
        layoutList: [
          {
            name: '图片导航',
            id: 'pic',
            icon: ico1
          },
          {
            name: '文字导航',
            id: 'txt',
            icon: ico2
          }
        ],
        //  每行显示几个的可配置数
        columnList: [
          {
            label: '3个导航',
            value: '3'
          },
          {
            label: '4个导航',
            value: '4'
          },
          {
            label: '5个导航',
            value: '5'
          },
          {
            label: '6个导航',
            value: '6'
          }
        ]
      }
    }
  }
</script>

<style scoped>
  .up-pic-list li {
    display: flex;
    padding: 15px;
    margin-bottom: 15px;
    background: #ffffff;
    border: 1px dashed #e5e5e5;
  }
  .up-pic-list li .pic {
    width: 80px;
    height: 80px;
    margin-right: 10px;
    border: 1px dashed #e5e5e5;
  }
  .up-pic-list li .pic img {
    width: 80px;
    height: 80px;
  }
  .up-pic-list li .pic .add-item-pic {
    width: 100%;
    height: 100%;
    color: #3366cc;
    cursor: pointer;
    text-align: center;
  }
  .up-pic-list li .pic .add-item-pic i {
    display: block;
    margin: 12px 0;
    font-size: 24px;
  }
  .up-pic-list li .info {
    flex: 1;
  }
  .add-pic {
    height: 80px;
    cursor: pointer;
    background: #fff;
    border: 1px dashed #e5e5e5;
    text-align: center;
  }

  .add-pic p {
    margin-top: 22px;
    color: #3366cc;
  }

  .add-info {
    padding-top: 20px;
    color: #999;
  }
</style>



