<template>
  <el-form :model="parmes" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="商品来源:">
      <el-radio-group v-model="parmes.source">
        <el-radio label="product">商品</el-radio>
        <el-radio label="group">商品分组</el-radio>
      </el-radio-group>
    </el-form-item>
    <div class="edit-white" v-if="parmes.source === 'product'">
      <el-form-item label="商品:">
        <div class="show-pic-sm">
          <img src="./pic_300.jpg">
          <i class="el-icon-error"></i>
        </div>
        <div class="add-pic-sm">
          <i class="el-icon-plus"></i>
        </div>
      </el-form-item>
    </div>
    <div class="edit-white" v-else-if="parmes.source === 'group'">
      <el-form-item label="商品分组:">
        <span class="fz_c">从商品分组中选择</span>
      </el-form-item>
      <el-form-item label="显示个数:">
        <el-input style="width: 100px"></el-input>
        <span class="ml_10 fc_999">最多显示50个</span>
      </el-form-item>
    </div>
    <el-form-item label="列表样式:">
      <el-radio-group v-model="parmes.style">
        <el-radio label="1">小图</el-radio>
        <el-radio label="2">详细列表</el-radio>
      </el-radio-group>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'productlist',
    props: ['parmes']
  }
</script>

<style scoped>
  .show-pic-sm {
    position: relative;
    float: left;
    margin: 0 10px 10px 0;
    width: 50px;
    height: 50px;
    border: 1px solid #ddd;
  }
  .show-pic-sm img {
    width: 100%;
    height: 100%;
  }
  .show-pic-sm i {
    position: absolute;
    right: -9px;
    top: -9px;
    width: 18px;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    background: #fff;
    color: #999;
    border-radius: 50%;
    cursor: pointer;
  }
  .add-pic-sm {
    float: left;
    width: 50px;
    height: 50px;
    line-height: 50px;
    color: #3366cc;
    cursor: pointer;
    text-align: center;
    border: 1px dashed #e5e5e5;
    font-size: 18px;
  }
</style>



