<template>
  <div class="index-notice" :style="{backgroundColor: parmes.backgroundColor}">
    <s class="ico"></s>
    <ul :style="{color: parmes.textColor}">
      <li>请选择展示的内容，如果选择多条，将会滚动展示</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'notice',
    props: ['parmes']
  }
</script>

<style scoped>
  .index-notice {
    position: relative;
    padding: .4rem .75rem .4rem 2rem;
    height: .7rem;
    line-height: .7rem;
    background: #fff7cc;
  }
  .index-notice .ico {
    position: absolute;
    left: .75rem;
    top: 50%;
    transform: translateY(-50%);
    width: .7rem;
    height: .7rem;
    background: url("notice.png") center no-repeat;
    background-size: 100% 100%;
  }
  .index-notice ul {
    color: #666;
  }
</style>



