<template>
  <ul class="product-list clearfix" :class="styleClass">
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <div class="info">
          <h3 class="name">舒适达 速效抗敏 牙膏 120g</h3>
          <h4 class="desc">此处显示商品描述</h4>
          <span class="price"><span class="make">￥</span>17.00</span>
        </div>
      </a>
    </li>
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <div class="info">
          <h3 class="name">高露洁（Colgate） 适齿炭牙刷 软毛 4支 （炫彩刷柄 软毛深洁）</h3>
          <h4 class="desc">此处显示商品描述</h4>
          <span class="price"><span class="make">￥</span>9.90</span>
          <del class="del"><span class="make">￥</span>17.00</del>
        </div>
      </a>
    </li>
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <div class="info">
          <h3 class="name">福临门 食用油 花生原香食用调和油5L 中粮出品</h3>
          <h4 class="desc">此处显示商品描述</h4>
          <span class="price"><span class="make">￥</span>49.00</span>
        </div>
      </a>
    </li>
    <li>
      <a href="##">
        <div class="pic">
          <img src="./pic_220_1.jpg"/>
        </div>
        <div class="info">
          <h3 class="name">帮宝适（Pampers）超薄干爽 婴儿纸尿裤 干爽绿帮 小号S164片【4-8kg】尿不湿</h3>
          <h4 class="desc">此处显示商品描述</h4>
          <span class="price"><span class="make">￥</span>149.00</span>
          <del class="del"><span class="make">￥</span>158.00</del>
        </div>
      </a>
    </li>
  </ul>
</template>

<script>
  export default {
    name: 'productlist',
    props: ['parmes'],
    computed: {
      styleClass() {
        var styleList = {'1': 'small-pic', '2': 'detail-list'}
        return styleList[this.parmes.style]
      }
    }
  }
</script>

<style scoped>
  /*商品列表通用样式*/
  .product-list {
    background: #fff;
    padding-bottom: .5rem;
  }
  .product-list li a {
    display: block;
    position: relative;
  }
  .product-list li .label {
    position: absolute;
    left: 0;
    top: 0;
  }
  .product-list li .pic img {
    width: 100%;
    height: 100%;
  }
  .product-list li .name {
    line-height: .8rem;
    height: 1.6rem;
    color: #666;
    overflow: hidden;
    margin-bottom: .3rem;
  }
  .product-list li .price {
    font-size: .6rem;
    font-weight: 700;
    margin-right: .2rem;
  }
  .product-list li .price .make {
    font-size: .5rem;
  }
  .product-more {
    display: block;
    text-align: center;
    color: #999;
    border-top: 1px solid #eee;
    line-height: 1.5rem;
    background: #fff;
  }

  .product-more s {
    display: inline-block;
    vertical-align: top;
    margin: .5rem 0 0 .2rem;
    width: .45rem;
    height: .45rem;
    background: url(arrow_right.png) no-repeat;
    background-size: auto 100%;
    opacity: .45;
  }

  /*小图样式*/
  .small-pic li {
    float: left;
    width: 47%;
    margin-left: 2%;
    margin-top: .5rem;
  }
  .small-pic li .pic {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    overflow: hidden;
    margin-bottom: .3rem;
  }
  .small-pic li .pic img {
    position: absolute;
  }
  .small-pic li .desc {
    display: none;
  }

  /*详细列表样式*/
  .detail-list li {
    width: 94%;
    margin: .5rem auto 0;
    border: 1px solid #ddd;
    overflow: hidden;
  }
  .detail-list li a {
    display: flex;
  }
  .detail-list li .pic {
    width: 5rem;
    height: 5rem;
  }
  .detail-list li .info {
    flex: 1;
    padding-top: .5rem;
    padding-left: .5rem;
  }
  .detail-list li .desc {
    line-height: .8rem;
    height: .8rem;
    color: #999;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: .5rem;
  }
</style>



