<template>
  <div>

    <ul class="up-pic-list">
      <li v-for="(item,index) in piclist" :key="index">
        <div class="pic"><img :src="item.src"></div>
        <div class="info">
          <el-form :model="item" label-width="50px" class="demo-ruleForm el-config-form">
            <el-form-item label="标题">
              <el-input v-model="item.title"></el-input>
            </el-form-item>
            <el-form-item label="链接">
              <span class="link-show" v-if="item.linkurl">
                外链
                <span class="link-url">{{item.linkurl}}</span>
                <i class="el-icon-close" @click="removeLink(item)"></i>
              </span>
              <el-dropdown @command="handleCommand">
                <span class="el-dropdown-link link-btn">
                  {{item.linkurl?'修改':'选择跳转到的页面'}}<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item command="a">商品列表</el-dropdown-item>
                  <el-dropdown-item command="a">商品详情</el-dropdown-item>
                  <el-dropdown-item command="a">资讯</el-dropdown-item>
                  <el-dropdown-item command="a">优惠券</el-dropdown-item>
                  <el-dropdown-item :command="{type:'customize',item:item}">自定义</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </el-form-item>
          </el-form>
        </div>
      </li>
    </ul>

    <!-- 新增部门模态框 -->
    <el-dialog title="自定义链接" :visible.sync="dialogCustomVisible">
      <el-form :model="customLinkForm" :rules="customLinkRules" ref="customLinkForm" label-width="70px"
               class="demo-ruleForm">
        <el-form-item label="链接地址" prop="link">
          <el-input v-model="customLinkForm.link" placeholder="http://"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('customLinkForm')">保 存</el-button>
        <el-button @click="dialogCustomVisible = false">取 消</el-button>
      </div>
    </el-dialog>
    <!-- 新增部门模态框 end -->

  </div>
</template>

<script>
  const linkBase = 'http://'
  export default {
    name: 'piclist',
    props: ['piclist'],
    data() {
      return {
        currentItem: '', // 当前操作项
        dialogCustomVisible: false, // 自定义链接模态框是否显示
        customLinkForm: {
          link: ''
        },
        customLinkRules: {
          link: [
            {required: true, message: '请输入链接地址', trigger: 'change'}
          ]
        }
      }
    },
    methods: {
      // 下拉选择链接地址
      handleCommand(command) {
        if (command.type === 'customize') {
          this.dialogCustomVisible = true
          this.currentItem = command.item
        }
      },
      // 自定义链接地址提交
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.currentItem.linkurl = linkBase + this.customLinkForm.link
            this.dialogCustomVisible = false
            this.customLinkForm.link = ''
          }
        })
      },
      // 删除链接地址
      removeLink(item) {
        item.linkurl = ''
      }
    }
  }
</script>

<style scoped>
  .up-pic-list li {
    display: flex;
    padding: 15px;
    margin-bottom: 15px;
    background: #ffffff;
    border: 1px dashed #e5e5e5;
  }

  .up-pic-list li .pic {
    width: 80px;
    height: 80px;
    margin-right: 10px;
  }

  .up-pic-list li .pic img {
    width: 80px;
    height: 80px;
  }

  .up-pic-list li .info {
    flex: 1;
  }

  .link-btn {
    color: #3366cc;
  }

  .link-show {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin: 3px 4px 0 0;
    padding: 1px 25px 1px 5px;
    line-height: 20px;
    max-width: 120px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    background: rgb(226, 243, 255);
    border: 1px solid rgba(51, 136, 255, 0.3);
  }

  .link-show .link-url {
    padding-left: 5px;
    margin-left: 3px;
    border-left: 1px solid #ddd;
  }

  .link-show .el-icon-close {
    position: absolute;
    right: 0;
    top: 0;
    width: 25px;
    line-height: 22px;
    text-align: center;
    cursor: pointer;
  }
</style>



