<template>
  <div>
    <el-form ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="选择模板">
        <ul class="layout-list">
          <li v-for="item in layoutList" :key="item.id" :class="{'active':parmes.layout == item.id}"
              @click="parmes.layout = item.id">
            <div class="pic"><img :src="item.icon"></div>
            <p class="name">{{item.name}}</p>
          </li>
        </ul>
      </el-form-item>
    </el-form>
    <piclist :piclist="parmes.piclist"></piclist>
    <div class="add-pic">
      <p><i class="el-icon-plus"></i> 添加一个广告图</p>
      建议尺寸 750x350 像素
    </div>
    <p class="add-info">最多添加10个广告，拖动图片广告可对其排序</p>
  </div>
</template>

<script>
  import ico1 from 'componentsconfig/swiper/lauout-banner-swiper.png'
  import ico2 from 'componentsconfig/swiper/lauout-banner-single.png'
  import piclist from 'componentsconfig/basic/piclist'

  export default {
    name: 'swiper',
    props: ['parmes'],
    data() {
      return {
        layoutList: [
          {
            name: '轮播广告',
            id: 'swiper',
            icon: ico1
          },
          {
            name: '1行1个',
            id: 'single',
            icon: ico2
          }
        ]
      }
    },
    components: {
      piclist
    }
  }
</script>

<style scoped>
  .add-pic {
    height: 80px;
    cursor: pointer;
    background: #fff;
    border: 1px dashed #e5e5e5;
    text-align: center;
  }

  .add-pic p {
    margin-top: 22px;
    color: #3366cc;
  }

  .add-info {
    padding-top: 20px;
    color: #999;
  }
</style>



